.round{
  display: none;
  position: fixed;
  bottom: 2rem;
  right: .5rem;
  width: 1rem;
  height: 1rem;
  text-align: center;
  line-height: 1rem;
  color: #fff;
  border-radius: 50%;
  background-color: rgba(0,0,0,0.6);
  -webkit-box-shadow: 0 0 20px #00a3ff;
          box-shadow: 0 0 20px #00a3ff;
  animation: blink_ .6s linear;
  -webkit-animation: blink_ .6s linear;
}
.round span{
  position: absolute;
  left: 12%;
  top: 12%;
  border-radius: 50%;
  display: block;
  width: .1rem;
  height: .1rem;
  background-color: #fff;
  -webkit-transform-origin: .4rem .4rem;
  -wevkit-animation: rod_ 2s linear infinite;
  -webkit-animation: rod_ 2s linear  infinite ;
          animation: rod_ 2s linear  infinite ;
}
.toggle{
  -webkit-transform: translateX(-2rem);
          transform: translateX(-2rem);
}
a{
  text-decoration: none;
  color: #000;
}
@-webkit-keyframes rod_ {
from {
    -webkit-transform: rotate(0deg);
}
to {
      -webkit-transform: rotate(360deg);
}
}
@keyframes rod_ {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
}
}
@-webkit-keyframes blink_ {
0% {opacity: 0.4;
}
15% {opacity: 1;
}
30% {opacity: 0.5;
}
45% {opacity: 1;
}
60% {opacity: 0.6;
}
75% {opacity: 1;
}
90% {opacity: 0.7;
}
100% {opacity: 1;
}
}
@keyframes blink_ {
0% {opacity: 0.4;
}
15% {opacity: 1;
}
30% {opacity: 0.5;
}
45% {opacity: 1;
}
60% {opacity: 0.6;
}
75% {opacity: 1;
}
90% {opacity: 0.7;
}
100% {opacity: 1;
}
}

