/*动画部分*/
.animation > div {
  z-index: 999;
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 1px solid #fff;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: rotateX(80deg) rotateY(20deg);
  transform-style: preserve-3d;
  transform: rotateX(80deg) rotateY(20deg);
}
.animation > div:first-of-type:after {
  content: "";
  position: absolute;
  height: .4rem;
  width: .4rem;
  background: #fff;
  border-radius: 50%;
  -webkit-transform: rotateX(-80deg) rotateY(0);
  transform: rotateX(-80deg) rotateY(0);
  -webkit-box-shadow: 0 0 25px #fff;
          box-shadow: 0 0 25px #fff;
  -webkit-animation: nucleus_ 2s infinite linear;
  animation: nucleus_ 2s infinite linear;
  left: 65%;
  top: 50%;
  margin-top: -.2rem;
  margin-left: -.35rem;
}
.animation > div:nth-of-type(2) {
  -webkit-transform: rotateX(-80deg) rotateY(20deg);
  transform: rotateX(-80deg) rotateY(20deg);
}
.animation > div:nth-of-type(3) {
  -webkit-transform: rotateX(-70deg) rotateY(60deg);
  transform: rotateX(-70deg) rotateY(60deg);
}
.animation > div:nth-of-type(4) {
  -webkit-transform: rotateX(70deg) rotateY(60deg);
  transform: rotateX(70deg) rotateY(60deg);
}
.animation > div > div {
  width: 1rem;
  height: 1rem;
  position: relative;
  -webkit-transform-style: preserve-3d;
  -webkit-animation: trail_ 2s infinite linear;
  transform-style: preserve-3d;
  animation: trail_ 2s infinite linear;
}
.animation > div > div:after {
  content: "";
  position: absolute;
  top: -5px;
  -webkit-box-shadow: 0 0 12px #fff;
          box-shadow: 0 0 12px #fff;
  left: 50%;
  margin-left: -5px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #fff;
  -webkit-animation: particle_ 2s infinite linear;
  animation: particle_ 2s infinite linear;
}
.animation > div:nth-of-type(2) > div,
.animation > div:nth-of-type(2) > div:after {
  -webkit-animation-delay: -.5s;
  animation-delay: -.5s;
}
.animation > div:nth-of-type(3)  > div,
.animation > div:nth-of-type(3)  > div:after {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
.animation > div:nth-of-type(4) > div,
.animation > div:nth-of-type(4) > div:after {
  -webkit-animation-delay: -1.5s;
  animation-delay: -1.5s;
}
@-webkit-keyframes trail_ {
from {
    -webkit-transform: rotateZ(0deg);
}
to {
      -webkit-transform: rotateZ(360deg);
}
}
@keyframes trail_ {
from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
to {
      -webkit-transform: rotateZ(360deg);
              transform: rotateZ(360deg);
}
}
@-webkit-keyframes particle_ {
from {
    -webkit-transform: rotateX(90deg) rotateY(0deg);
    transform: rotateX(90deg) rotateY(0deg);
}
to {
      -webkit-transform: rotateX(90deg) rotateY(-360deg);
      transform: rotateX(90deg) rotateY(-360deg);
}
}
@keyframes particle_ {
from {
    -webkit-transform: rotateX(90deg) rotateY(0deg);
            transform: rotateX(90deg) rotateY(0deg);
}
to {
      -webkit-transform: rotateX(90deg) rotateY(-360deg);
              transform: rotateX(90deg) rotateY(-360deg);
}
}
@keyframes nucleus_ {
0% {
    -webkit-box-shadow: 0 0 0 transparent;
            box-shadow: 0 0 0 transparent;
}
50% {
      -webkit-box-shadow: 0 0 25px #fff;
              box-shadow: 0 0 25px #fff;
}
100% {
        -webkit-box-shadow: 0 0 0 transparent;
                box-shadow: 0 0 0 transparent;
}
}
@-webkit-keyframes nucleus_ {
